<template>
  <div class="star" :class="`star-${size}`">
    <span class="star-item" v-for="(star,index) in starItem" :key="index" :class="star"></span>
  </div>
</template>
<script>
  const STAR_ON = 'on';
  const STAR_HALF = 'half';
  const STAR_OFF = 'off';
  export default {
    props:{
      score: Number,
      size : Number
    },
    computed:{
      //创建一个数组来保存星星的状态，知道要显示什么
      starItem(){
        const starArr = [];
        const starInter = Math.floor(this.score);
        for(let i= 0; i<starInter; i++){
          starArr.push(STAR_ON)
        }
        if(this.score * 10 - starInter * 10 >=5){
          starArr.push(STAR_HALF);
        }
        while(starArr.length <5){
          starArr.push(STAR_OFF);
        }
        return starArr;
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixins.styl"
  .star //2x图 3x图
    float left
    font-size 0
    .star-item
      display inline-block
      background-repeat no-repeat
    &.star-48
      .star-item
        width 20px
        height 20px
        margin-right 22px
        background-size 20px 20px
        &:last-child
          margin-right: 0
        &.on
          bg-image('./images/stars/star48_on')
        &.half
          bg-image('./images/stars/star48_half')
        &.off
          bg-image('./images/stars/star48_off')
    &.star-36
      .star-item
        width 15px
        height 15px
        margin-right 6px
        background-size 15px 15px
        &:last-child
          margin-right 0
        &.on
          bg-image('./images/stars/star36_on')
        &.half
          bg-image('./images/stars/star36_half')
        &.off
          bg-image('./images/stars/star36_off')
    &.star-24
      .star-item
        width 10px
        height 10px
        margin-right 3px
        background-size 10px 10px
        &:last-child
          margin-right 0
        &.on
          bg-image('./images/stars/star24_on')
        &.half
          bg-image('./images/stars/star24_half')
        &.off
          bg-image('./images/stars/star24_off')
</style>



